#quotes-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0px 50px 0px 50px;
    i {
        color: #00cc00;
        margin: 0px 10px;
        font-size: 5em;      
    }
    .content {
        font-size: 1.5em;
        text-align: left;

