button {
    font-family: 'Rock Salt', cursive;
    padding: 10px 40px;
    font-size: 1.5em;
    color: #00cc00;
    border-color: #00cc00;
    background: none;
    border-radius: 2px;
}
footer {
    ul{
        display: flex;
        position: relative;
        align-content: center;
        justify-content: space-around;
        font-size: 1.3em;      
        li{
            list-style-type: none;
            color:#00cc00;
            padding: 15px;
            text-decoration: none;
            a{
                text-decoration: none;
                color:#00cc00;
            }
        }
    }
    p {
        color: #00cc00;
        span{
            color: white;
            padding: 0px 15px 0px 0px;
        }
    }
}
.submit-section {
    margin: 0px;
    font-size: 2em;
}
#submit-button{
    margin-top: 30px;
    border-radius: 2px;
}
form {
    display: flex;
    flex-direction: column;

    #quote-title{
        margin: 0px 300px;
        background-color: #222222;
        border: 1px solid white;
        color: white;    
        border-radius: 2px;
    }
    #quote-content{
        margin: 0px 300px;
        background-color: #222222;
        border: 1px solid white;
        color: white; 
        border-radius: 2px;
    }
    #quote-source{
        margin: 0px 300px;
        background-color: #222222;
        border: 1px solid white;
        color: white; 
        border-radius: 2px;
    }
    #quote-url{
        margin: 0px 300px;
        background-color: #222222;
        border: 1px solid white;
        color: white;
        border-radius: 2px;
    }

}

#about-title{
    h2{
    align-items: center;
}
}

